import SignInPage from './pages/SignInPage'
import SignUpPage from './pages/SignUpPage'
import VerificationEmailPage from './pages/VerificationEmailPage'
import Container from 'react-bootstrap/Container'
import { Switch, Route } from 'react-router-dom'
import SaraQuestionsPage from './pages/SaraQuestionsPage'
import SafeSpaceSpecificationsPage from './pages/SafeSpaceSpecificationsPage'
import HomePage from './pages/HomePage'
function App() {
  return (
    <Container>
      <Switch>
        <Route exact path='/'>
          <HomePage />
        </Route>
        <Route path='/sign-in'>
          <SignInPage />
        </Route>
        <Route path='/sign-up/:signUpType?'>
          <SignUpPage />
        </Route>
        <Route path='/questions'>
          <SaraQuestionsPage />
        </Route>
        <Route path='/submitted'>
          <VerificationEmailPage />
        </Route>
        <Route path='/safe-space-specifications'>
          <SafeSpaceSpecificationsPage />
        </Route>
      </Switch>
    </Container>
  )
}

export default App
